@import 'constants';
@import 'tools/styles/flex';
@import 'tools/styles/adaptive';

.card {
    @include dFlex();
    @include flexRow();
    @include justifyContentCenter();
    @include alignItemsCenter();

    position: relative;

    border: 1px solid $borderBlue;
    background: $black;

    z-index: 2;

    .image {
        position: absolute;

        top: 0;
        left: 0;

        z-index: 0;

        width: 100%;
        height: 100%;
    }
    .title {
        font-style: normal;
        font-weight: 700;

        color: $white;
        text-align: center;
        text-transform: uppercase;

        z-index: 40;
    }
}

