@import 'constants';
@import 'tools/styles/flex';
@import 'tools/styles/adaptive';

.socialButton {
    @include dFlex();
    @include flexRow();
    @include alignItemsCenter();

    position: relative;
    z-index: 10;

    width: fit-content;
    background: $black;
    border: 1px solid $borderBlue;

    font-style: normal;
    font-weight: 400;
    color: $white;
    text-decoration: unset;
    cursor: pointer;

    padding: 14px 25px 14px 25px;

    img {
        width: 24px;
        height: 24px;

        margin-left: 18px;
    }
}

@include applyResponsive320() {
    .socialButton {
        font-size: 11px;
        line-height: 13px;
    }
}

@include applyResponsive414() {
    .socialButton {
        font-size: 14px;
        line-height: 16px;
    }
}

@include applyResponsive768() {
    .socialButton {
        font-size: 20px;
        line-height: 22px;
    }
}

@include applyResponsive1920() {
    .socialButton {
        font-size: 24px;
        line-height: 28px;
    }
}
