@import 'constants';
@import 'tools/styles/helpers';

body {
    margin: 0;
    font-family:
        'Roboto Condensed',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    @include customScrollbar() {
        width: 7px;
        padding-right: 10px;
    }
    @include customScrollThumb() {
        @include borderRadius(8px);

        width: 3px;
        background: $darkGrey;
    }
}

code {
    font-family:
        'Roboto Condensed',
        monospace;
}

* {
    box-sizing: border-box;
}