@import 'constants';
@import 'tools/styles/flex';
@import 'tools/styles/helpers';
@import 'tools/styles/adaptive';

.home {
    position: relative;
    overflow: hidden;

    width: 100%;
    min-height: 100vh;

    background: $black;

    .smallLogo {
        @include dFlex();
        @include flexRow();
        @include justifyContentCenter();

        img {
            position: relative;

            z-index: 10;
        }
    }
    .main {
        @include dFlex();
        @include flexRow();
        @include justifyContentCenter();
        @include alignItemsCenter();

        .logo {
            img {
                position: relative;

                z-index: 10;
            }
        }
        .title {
            z-index: 2;

            font-style: normal;
            font-weight: 700;

            color: $white;
            text-transform: uppercase;
        }
    }
    .descriptionContainer {
        @include dFlex();
        @include flexRow();
        @include justifyContentCenter();

        .description {
            font-style: normal;
            font-weight: 800;

            color: $white;

            .red {
                color: $red;
            }
        }
    }
    .cards {
        @include dFlex();

        .card {

        }
        .cardCenter {

        }
    }
    .socialContainer {
        @include dFlex();
        @include flexRow();
        @include justifyContentCenter();
    }
}

@include applyResponsive320() {
    .home {
        padding-top: 71px;
        padding-bottom: 56px;

        .smallLogo {
            margin-bottom: 52px;

            img {
                width: 91px;
                height: 102px;
            }
        }

        .main {
            margin-bottom: 12px;

            .logo {
                display: none;

                img {

                }
            }

            .title {
                font-size: 35px;
                line-height: 42px;
            }
        }

        .descriptionContainer {
            .description {
                width: 302px;

                font-size: 16px;
                line-height: 19px;

                margin-bottom: 63px;

                .red {
                    margin-right: 5px;
                }
                .br {
                    display: block;
                }
            }
        }

        .cards {
            @include flexColumn();
            @include alignItemsCenter();

            margin-bottom: 70px;

            .card {
                width: 302px;
                height: 143px;
            }

            .cardCenter {
                width: 302px;
                height: 143px;
            }
        }
    }
    .socialContainer {

    }
}

